.couple-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	hr {
		position: relative;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 240px;
		transform: translate(-40%, -10%);
		@include max-md {
			width: 200px;
		}
		div:nth-child(1) {
			position: absolute;
			top: -2rem;
			right: 0;
			width: 60%;
			z-index: -1;
			@include max-lg {
				width: 60%;
				bottom: 2rem;
			}
			@include max-md {
				width: 50%;
				bottom: unset;
				top: -1rem;
			}
			img {
				transform-origin: 50% 100%;
			}
		}
		img {
			transform-origin: 80% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		top: 0;
		right: 0;
		width: 250px;
		transform: translate(40%, -30%);
		@include max-md {
			transform: translate(40%, -40%);
		}
		@include max-md {
			width: 200px;
		}
		img {
			transform-origin: 20% 100%;
		}
	}
	.elements-absolute {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.background-element {
			display: block;
			height: 100%;
			.background-wrapper {
				width: 100%;
				height: 100%;
				display: inline-block;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					border-radius: var(--border-radius);
				}
			}
		}
	}
	.and {
		// @include min-md {
		// 	display: none;
		// }
		font-weight: 600;
		color: var(--color-dark);
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100%;
		position: relative;
		z-index: 3;
		span {
			position: relative;
			font-size: 24px;
			font-family: $font-secondary;
		}
	}
	.couple-content {
		position: relative;
		border-radius: var(--border-radius);
	}
	.couple {
		position: relative;
		text-align: center;
		padding: 1rem 0;
		border-radius: 12px;
		height: 100%;
		&.single {
			margin-top: 10rem;
			.image-wrap {
				margin-top: -10rem;
				aspect-ratio: 1;
			}
			.couple-description {
				background-color: transparent;
			}
		}
		.image-wrap {
			width: 100%;
			position: relative;
			aspect-ratio: 5/6;
			border-radius: var(--border-radius);
			.couple-image {
				height: 100%;
				object-fit: cover;
				position: relative;
				width: 100%;
				border-radius: var(--border-radius);
			}
		}
		.couple-description {
			position: relative;
			display: flex;
			flex-direction: column;
			align-items: center;
			justify-content: center;
			height: 100%;
			border-radius: var(--border-radius);
			padding: 2rem 1rem;
			z-index: 5;
			background-color: var(--color-primary);
			.couple-body {
				width: fit-content;
				max-width: 100%;
				margin: 0 auto;
			}
		}
		.couple-parent {
			width: 100%;
		}
		.sosmed-wrap {
			position: relative;
			width: 100%;
			.sosmed {
				color: var(--color-button-trans);
				text-decoration: none;
				padding: 0.5rem 1.5rem;
				border-radius: 50px;
				background: transparent;
				border: 1px solid var(--color-button-trans);
				transition: all 0.25s ease;
				display: block;
				width: fit-content;
				margin: 0 auto;
				&:hover {
					background: var(--color-secondary);
					color: var(--text-button);
					border: 1px solid var(--color-secondary);
				}
				small {
					align-items: center;
					gap: 0.25rem;
					display: flex;
					justify-content: center;
				}
			}
			.fab {
				font-size: 18px;
				@include max-md {
					font-size: 16px;
				}
			}
		}
	}
	.reminder-wrap {
		padding: 3rem 0 0;
		.background-element {
			display: block;
			width: 100%;
			height: 380px;
			overflow: hidden;
			.background-wrapper {
				width: 100%;
				height: 100%;
				display: inline-block;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
					border-radius: 0 !important;
				}
			}
		}
		@include max-md {
			.elements-absolute {
				.background-element {
					height: 100%;
				}
			}
		}
		.reminder-content {
			position: relative;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			padding: 0 0 0 3rem;
			gap: 2rem;
			@include max-lg {
				padding: 0 0 0 2rem;
			}
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
