.thank-section {
	position: relative;
	padding: 5rem 0;
	text-align: center;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		right: 0;
		width: 180px;
		transform: translate(20%, -50%) scaleX(-1);
		@include max-md {
			width: 140px;
		}
		img {
			transform-origin: 80% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		left: 0;
		transform: translate(-30%, 30%) scaleY(-1);
		width: 250px;
		img {
			transform-origin: 15% 100%;
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 6/4;
		overflow: hidden;
		border-radius: var(--border-radius);
		@include max-md {
			aspect-ratio: 1;
		}
		.thank-background {
			width: 100%;
			height: 100%;
			object-fit: cover;
			border-radius: var(--border-radius);
		}
	}
	.thank-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		@include max-md {
			.row {
				gap: 2rem;
			}
		}
		.thank {
			position: relative;
			padding: 3rem 2rem;
			height: 100%;
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			z-index: 5;
			@include max-md {
				padding: 2rem 1rem;
			}
			&::before {
				position: absolute;
				top: 0;
				right: 0;
				bottom: 0;
				left: 0;
				background-color: var(--color-primary);
				opacity: 0.7;
				content: "";
				border-radius: var(--border-radius);
			}
		}
	}
}
.footer {
	padding: 1rem 0;
	position: relative;
	text-align: center;
	background-color: var(--color-secondary);
	color: var(--text-button);
	small {
		position: relative;
	}
}
