.rsvp-section {
	position: relative;
	padding: 5rem 0;
	@include max-md {
		padding: 3rem 0;
	}
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-30%, -30%);
		@include max-md {
			width: 140px;
		}
		img {
			transform-origin: 60% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 250px;
		transform: translate(40%, 30%);
		@include max-md {
			width: 220px;
		}
		img {
			transform-origin: 35% 100%;
		}
	}
	.rsvp-wrapper {
		position: relative;
		border-radius: var(--border-radius);
		overflow: hidden;
		background-image: url(../images/bg-rsvp.webp);
		background-size: cover;
		background-repeat: no-repeat;
		background-position: center;
		padding: 3rem 0;
		background-color: var(--color-primary);
		@include max-lg {
			padding: 1rem 0;
		}
		.rsvp-form {
			padding: 2rem;
			position: relative;
			@include max-md {
				padding: 2rem 1rem;
			}
		}
	}
}
