.cover-section {
	position: fixed;
	z-index: 1000;
	transition: all 1s ease-out;
	background-color: var(--color-main);
	overflow: hidden;
	height: 100vh;
	width: 100%;
	.orn-1 {
		position: absolute;
		top: 0;
		left: 0;
		width: 180px;
		transform: translate(-40%, -40%);
		@include max-md {
			width: 160px;
		}
	}
	.orn-2 {
		position: absolute;
		bottom: 0;
		right: 0;
		width: 220px;
		transform: translate(40%, 30%);
		@include max-md {
			width: 200px;
		}
	}
	.cover-wrapper {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: flex-start;
		padding: 4rem 0 3rem;
		@include max-lg {
			padding: 4rem 0;
			align-items: center;
		}
		.cover-content {
			text-align: center;
			position: relative;
			background-color: transparent;
			border-radius: 12px;
			color: var(--color-dark);
			height: 100%;
			@include max-lg {
				display: flex;
				align-items: center;
				justify-content: center;
			}
			@include max-mhc(667px) {
				align-items: flex-start;
			}
			.cover-body {
				border-radius: 12px;
				position: relative;
				height: 90%;
				@include max-lg {
					width: 100%;
					height: auto;
				}
				.greeting-wrapper {
					position: absolute;
					@include max-md {
						font-size: 14px;
					}
					h6 {
						font-weight: 700;
					}
				}
				.image-wrapper {
					position: relative;
					width: auto;
					height: 100%;
					aspect-ratio: 6/4;
					border-radius: 12px;
					@include max-lg {
						width: 100%;
						height: auto;
						aspect-ratio: 1;
					}
					@include max-md {
						aspect-ratio: 4/3;
					}
					.cover-background {
						width: 100%;
						height: 100%;
						object-fit: cover;
						border-radius: 12px;
					}
				}
				.cover-description {
					padding: 2.5rem 1.5rem 0;
					text-align: center;
					position: absolute;
					bottom: 0;
					left: 50%;
					transform: translateX(-50%);
					min-width: 320px;
					&::before {
						position: absolute;
						top: 0;
						left: 0;
						right: 0;
						bottom: 0;
						content: "";
						background-color: var(--color-primary);
						opacity: 0.7;
						border-top-left-radius: var(--border-radius);
						border-top-right-radius: var(--border-radius);
						@include max-md {
							border-radius: var(--border-radius);
						}
					}
					@include max-md {
						position: relative;
						top: unset;
						left: unset;
						width: 80%;
						margin: -2rem auto 0;
						transform: none;
						min-width: auto;
					}
					@include max-xs {
						width: 90%;
					}
					* {
						position: relative;
					}
				}
			}
			.elements-widget {
				width: 100%;
				.btn-custom {
					transform: translateY(50%);
				}
			}
		}
	}
}
