.header-section {
	position: relative;
	height: 100vh;
	width: 100%;
	.scroll-icon {
		position: absolute !important;
		top: 100%;
		left: 50%;
		transform: translate(-50%, -50%);
		z-index: 5;
	}
	.orn-1 {
		position: absolute;
		bottom: 0;
		left: 0;
		width: 200px;
		transform: translate(-50%, 0);
		@include max-md {
			width: 160px;
		}
		img {
			transform-origin: 100% 100%;
		}
	}
	.orn-2 {
		position: absolute;
		right: 0;
		top: 0;
		width: 250px;
		transform: translate(50%, -10%);
		@include max-lg {
			transform: translate(40%, -30%);
		}
		@include max-md {
			width: 200px;
		}
		img {
			transform-origin: 20% 100%;
		}
	}
	.elements-absolute {
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		.background-element {
			display: block;
			height: 100%;
			.background-wrapper {
				width: 100%;
				height: 100%;
				display: inline-block;
				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}
	}
	.image-wrapper {
		position: relative;
		width: 100%;
		height: auto;
		aspect-ratio: 1;
		border-radius: var(--border-radius);
		margin: 0 auto;
		.image-element {
			position: relative;
			width: 100%;
			height: 100%;
			.background-header {
				width: 100%;
				height: 100%;
				object-fit: cover;
				border-radius: 12px;
			}
		}
	}
	.header-content {
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		.header {
			position: relative;
			display: block;
			width: 50%;
			padding: 3rem 0;
			@include max-lg {
				width: 80%;
			}
			@include max-md {
				width: 100%;
				padding: 4rem 0;
			}
			.header-title {
				position: absolute;
				top: 50%;
				left: 90%;
				min-width: 320px;
				transform: translateY(-50%);
				text-align: center;
				@include max-lg {
					position: relative;
					top: unset;
					left: unset;
					transform: translateY(-3rem);
					width: 80%;
					min-width: auto;
					margin: 0 auto;
				}
				@include max-md {
					transform: translateY(-2rem);
				}
				&-content {
					position: relative;
					padding: 3rem 2rem;
					border-radius: var(--border-radius);
					@include max-md {
						padding: 2rem 1rem;
					}
					&::before {
						position: absolute;
						top: 0;
						right: 0;
						bottom: 0;
						left: 0;
						content: "";
						background-color: var(--color-primary);
						opacity: 0.7;
						border-radius: var(--border-radius);
					}
					* {
						position: relative;
					}
				}
			}
			#scroll-animate {
				animation: scrollAnim 1500ms ease infinite;
			}
		}
	}
}

@keyframes scrollAnim {
	0% {
		opacity: 0;
		transform: translate3d(0, -20%, 0);
	}
	50% {
		opacity: 1;
	}
	100% {
		transform: translate3d(0, 100%, 0);
		opacity: 0;
	}
}
